import React, { useEffect, useState } from 'react';

const Tempapp=()=> {
    const [search, setsearch] = useState("mumbai")
    const [city, setcity] = useState();

    useEffect(()=>{
        const fetchApi=async()=>{
            const url = `http://api.openweathermap.org/data/2.5/weather?q=${search}&units=metric&appid=1dfe0edf90143e51659a16ea30c7267f`
            const res = await fetch(url)
            const resJson =await res.json()
            // console.log(resJson)
            setcity(resJson.main);
        }

        fetchApi();
    },[search] )
    
    return (
        <>
            <div className="box">
                <div className="input">
                    <input type="search" placeholder="search ðŸ”Ž" value={search} onChange={(event)=>{setsearch(event.target.value)}} className="inputField"/> 
                </div>


                {!city ? (<p>No Data Found</p>) : 
                    (<div className="info">
                        <div className="location">
                        <i class="fas fa-street-view"> </i>
                        <h1>{search}</h1> 
                        </div>

                        <h2 className="temp">
                            {city.temp}`Cel
                        </h2>

                        <h3 className="tempmin_max">
                            Min : {city.temp_min}`Cel | Max : {city.temp_max}`Cel
                        </h3>
                        
                    </div>)
                }

                <div className="wave -one "></div>
                <div className="wave -two "></div>
                <div className="wave -three "></div>
            </div>       
        </>
    )
}

export default Tempapp;


